import React, { Component } from 'react'
import { array } from 'prop-types'

// Third party library
import Fade from 'react-reveal/Fade'
import Gallery from 'react-photo-gallery'
import Lightbox from 'react-images'

export default class ShowRooms extends Component {
  static propTypes = {
    showRooms: array,
  }

  state = {
    currentImage: 0,
  }

  openLightbox = (event, obj) =>
    this.setState({
      currentImage: obj.index,
      lightboxIsOpen: true,
    })

  closeLightbox = () =>
    this.setState({
      currentImage: 0,
      lightboxIsOpen: false,
    })

  gotoPrevious = () =>
    this.setState({
      currentImage: this.state.currentImage - 1,
    })

  gotoNext = () =>
    this.setState({
      currentImage: this.state.currentImage + 1,
    })

  render() {
    const { showRooms } = this.props
    const photos = showRooms.reduce(
      (pre, cur) => [...pre, { src: cur, width: 4, height: 3 }],
      [],
    )
    return (
      <Fade bottom>
        <div className="cls-floorplan-wrapper mb-4">
          <h3 className="cls-header3 mb-4">Explore Environment</h3>
          <div className="row">
            <Gallery photos={photos} onClick={this.openLightbox} />
            <Lightbox
              images={photos}
              onClose={this.closeLightbox}
              onClickPrev={this.gotoPrevious}
              onClickNext={this.gotoNext}
              currentImage={this.state.currentImage}
              isOpen={this.state.lightboxIsOpen}
            />
          </div>
        </div>
      </Fade>
    )
  }
}



