import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'

export default class VideoOverview extends Component {
  render() {
    return (
      <Fade bottom>
        <div className="cls-video-wrapper mb-4">
          <h3 className="cls-header3 mb-4">Video Overview</h3>
          <div className="row">
            <div className="col-12 col-sm-12 mb-4">
              <div>
                <iframe
                  width="100%"
                  height="400"
                  src="https://www.youtube.com/embed/f4vW_uv8BTk"
                  frameBorder="0"
                  allowFullScreen
                />
              </div>
            </div>
          </div>
        </div>
      </Fade>
    )
  }
}



