import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
export default class AboutDetails extends Component {
  render() {
    return (
      <section id="sec-about">
        <div className="container">
          <Fade bottom>
            <div align="center">
              <img
                src={require('../../../assets/images/group-photo.jpg')}
                className="img-fluid mb-3"
              />
            </div>
          </Fade>
          <Fade bottom>
            {' '}
            <h3 className="cls-header3 mb-3">Why choose Us</h3>
          </Fade>
          <Fade bottom>
            {' '}
            <div className="cls-vision-content mb-3">
              We are 24 hours operating. Our Main focus is on New Project (Own
              Stay/Investment) Our Service included:
              <ol>
                <li>Analyze Current Property</li>
                <li>Provide Free Customer Service</li>
                <li>Comparing Different Location Property</li>
                <li>Provide Updated to Date Property</li>
                <li>Guide Step by Step to Own a Dream House</li>
              </ol>
            </div>
          </Fade>
          <div className="height50" />
          <Fade bottom>
            <div className="row">
              <div className="col-12 col-sm-6">
                <div className="cls-vision-wrapper">
                  <div className="cls-vision-content-wrapper">
                    <div className="cls-vision-icon">
                      <span className="cls-txt-icon">
                        <i className="fas fa-bullseye fa-2x" />
                      </span>
                    </div>

                    <h3 className="cls-header3 mt-3">Vision</h3>
                    <div className="cls-vision-content">
                      <ul>
                        <li>
                          Providing more value to clienteles and double the
                          investment return.
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-12 col-sm-6">
                <div className="cls-vision-wrapper">
                  <div className="cls-vision-content-wrapper">
                    <div className="cls-vision-icon">
                      <span className="cls-txt-icon">
                        <i className="fas fa-bullseye fa-2x" />
                      </span>
                    </div>

                    <h3 className="cls-header3 mt-3">Mission</h3>
                    <div className="cls-vision-content">
                      <ul>
                        <li>Assist clienteles to found their dream homes.</li>
                        <li>
                          Analyse the right properties to achieve clienteles
                          investment portfolio.
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </Fade>
        </div>
      </section>
    )
  }
}



