import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
// Const
import {
  featurePropertyOne,
  featurePropertyTwo,
  featurePropertyThree,
  featurePropertyFour,
} from '../../../data/const'

// Components
import PropertyCard from '../../../components/PropertyCard'

export default class AllDetails extends Component {
  render() {
    return (
      <section id="feature-section">
        <div className="container" align="middle">
          <div>
            <Fade bottom>
              {' '}
              <div className="row">
                {featurePropertyOne.map((item, i) => (
                  <PropertyCard
                    key={i}
                    link={item.link}
                    image={item.img}
                    name={item.name}
                    details={item.details}
                    price={item.price}
                  />
                ))}
              </div>
            </Fade>
            <Fade bottom>
              <div className="row">
                {featurePropertyTwo.map((item, i) => (
                  <PropertyCard
                    key={i}
                    link={item.link}
                    image={item.img}
                    name={item.name}
                    details={item.details}
                    price={item.price}
                  />
                ))}
              </div>
            </Fade>
            <Fade bottom>
              <div className="row">
                {featurePropertyThree.map((item, i) => (
                  <PropertyCard
                    key={i}
                    link={item.link}
                    image={item.img}
                    name={item.name}
                    details={item.details}
                    price={item.price}
                  />
                ))}
              </div>
            </Fade>
            <Fade bottom>
              <div className="row">
                {featurePropertyFour.map((item, i) => (
                  <PropertyCard
                    key={i}
                    link={item.link}
                    image={item.img}
                    name={item.name}
                    details={item.details}
                    price={item.price}
                  />
                ))}
              </div>
            </Fade>
          </div>
        </div>
      </section>
    )
  }
}



