import React, { Component } from 'react'
import { string } from 'prop-types'
import Fade from 'react-reveal/Fade'

export default class ContactAgent extends Component {
  static propTypes = {
    name: string,
    email: string,
    mobile: string,
    image: string,
    message: string,
  }
  render() {
    const { name, email, mobile, image, message } = this.props
    return (
      <Fade bottom>
        {' '}
        <div className="cls-agent-wrapper mb-4">
          <div className="cls-agent-image">
            <img src={image} className="img-fluid" />
          </div>
          <div className="cls-agent-name mb-3">{name}</div>
          <div className="cls-agent-desc mb-2">{message}</div>
          <div className="cls-contact-number">
            <i className="fa fa-phone-square" aria-hidden="true" />{' '}
            <a href={`tel:${mobile}`}>{mobile}</a>
          </div>
          <div className="cls-agent-email">
            <i className="fa fa-envelope-square" aria-hidden="true" />{' '}
            <a href={`mailto:${email}`}>{email}</a>
          </div>
        </div>
      </Fade>
    )
  }
}



