import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
// Google Map React
import GoogleMapReact from 'google-map-react'
const Map = ({ text }) => <div>{text}</div>

export default class ContactMap extends Component {
  render() {
    const center = {
      lat: 5.32,
      lng: 100.28,
    }
    return (
      <Fade bottom>
        <section id="sec-map">
          <div id="map">
            <GoogleMapReact
              bootstrapURLKeys={{
                key: 'AIzaSyCD2DLCs7OSSqTesVwenzEex8tf7nkd-5k',
              }}
              defaultCenter={center}
              defaultZoom={11}
            >
              <Map
                lat={5.32831}
                lng={100.28476}
                text={'IQI Realty Sdn Bhd (Penang Branch)'}
              />
            </GoogleMapReact>
          </div>
        </section>
      </Fade>
    )
  }
}



