import React, { Component } from 'react'
import { string } from 'prop-types'
import Fade from 'react-reveal/Fade'

export default class Download extends Component {
  static propTypes = {
    pathname: string,
  }
  render() {
    const { pathname } = this.props
    return (
      <Fade bottom>
        <div className="cls-price-wrapper mb-4">
          <h3 className="cls-header3 mb-3">Brochure</h3>
          <div>Click to get latest brochure</div>
          <a
            href={`https://api.whatsapp.com/send?phone=60164637218&text=Hi%20WeiLoong,%20Can%20i%20get%20the%20${pathname}%20PDF%20File?%20Thank%20You!#`}
            target="_blank"
            className="btn btn-primary btn-block"
          >
            <i className="fas fa-file-pdf" /> Here
          </a>
        </div>
      </Fade>
    )
  }
}



