import React, { Component } from 'react'
import Fade from 'react-reveal/Fade'
import '../../../assets/js/script'
// ReactStrap
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
} from 'reactstrap'

// Const
import { slideShow } from '../../../data/const'

export default class SliderComponent extends Component {
  state = {
    activeIndex: 0,
    animating: false,
  }

  onExiting = () => {
    this.setState({
      animating: true,
    })
  }

  onExited = () => {
    this.setState({
      animating: false,
    })
  }

  next = () => {
    if (this.state.animating) return
    const nextIndex =
      this.state.activeIndex === slideShow.length - 1
        ? 0
        : this.state.activeIndex + 1
    this.setState({ activeIndex: nextIndex })
  }

  previous = () => {
    if (this.state.animating) return
    const nextIndex =
      this.state.activeIndex === 0
        ? slideShow.length - 1
        : this.state.activeIndex - 1
    this.setState({ activeIndex: nextIndex })
  }

  goToIndex = newIndex => {
    if (this.state.animating) return
    this.setState({ activeIndex: newIndex })
  }

  render() {
    const slides = slideShow.map((item, i) => {
      return (
        <CarouselItem
          key={i}
          onExiting={this.onExiting}
          onExited={this.onExited}
          className={item.class}
        >
          {/*<div className={item.class} align="middle">*/}
          {/*<div className="banner-slide-container all-btn" align="middle">*/}
          {/*<h1 className="banner-title">*/}
          {/*<small>Residential</small>*/}
          {/*{item.caption}*/}
          {/*</h1>*/}
          {/*<p>RM {item.price}</p>*/}
          {/*<Link to={item.link}>MORE DETAILS</Link>*/}
          {/*</div>*/}
          {/*</div>*/}
          <Fade bottom>
            <div className="cls-carousel-wrapper">
              <div className="cls-property-name">Residential</div>
              <div className="cls-property-subname">{item.caption}</div>
              <div className="cls-property-price">
                <span className="cls-txt-from">from</span> {item.price}
              </div>
            </div>
          </Fade>
        </CarouselItem>
      )
    })

    return (
      <Carousel
        activeIndex={this.state.activeIndex}
        next={this.next}
        previous={this.previous}
      >
        <CarouselIndicators
          items={slideShow}
          activeIndex={this.state.activeIndex}
          onClickHandler={this.goToIndex}
        />
        {slides}
        <CarouselControl
          direction="prev"
          directionText="Previous"
          onClickHandler={this.previous}
        />
        <CarouselControl
          direction="next"
          directionText="Next"
          onClickHandler={this.next}
        />
      </Carousel>
    )
  }
}



